import React, { useState } from "react";
import { Button, Input } from "@vkontakte/vkui";
import data from "../data";
import { setNotes } from "../../database";

export default function EditNotesTitle({
  prevTitle,
  setEditNotesTitleShowing,
}) {
  const [value, setValue] = useState("");

  function submitHandler(event) {
    event.preventDefault();

    if (value.trim()) {
      var notesId = data.notesId;
      var notes = "";
      var result = "";
      if (notesId === "1") {
        notes = data.notes1;
      } else if (notesId === "2") {
        notes = data.notes2;
      } else if (notesId === "3") {
        notes = data.notes3;
      } else {
        notes = data.notes4;
      }

      notes
        .split("₪")
        .slice(1)
        .map((item) => {
          result += "₪" + item;
          return true;
        });
      result = value + result;

      if (notesId === "1") {
        data.notes1 = result;
      } else if (notesId === "2") {
        data.notes2 = result;
      } else if (notesId === "3") {
        data.notes3 = result;
      } else {
        data.notes4 = result;
      }
      setNotes(
        data.docId,
        data.userId,
        data.notes1,
        data.notes2,
        data.notes3,
        data.notes4
      );
    }
    setEditNotesTitleShowing(false);
  }

  const style = {
    display: "flex",
    flexDirection: "row",
    flex: 1,
    justifyContent: "space-between",
    alignItems: "center",
    margin: "1rem",
  };

  return (
    <form onSubmit={submitHandler} style={style}>
      <Input
        placeholder={prevTitle}
        value={value}
        onChange={(event) => setValue(event.target.value)}
      />
      <Button type="submit" size="l">
        ОК
      </Button>
    </form>
  );
}
