import React, { useState } from "react";
import PropTypes from "prop-types";
import {
  platform,
  IOS,
  Div,
  Panel,
  PanelHeader,
  PanelHeaderButton,
} from "@vkontakte/vkui";
import Icon28ChevronBack from "@vkontakte/icons/dist/28/chevron_back";
import Icon24Back from "@vkontakte/icons/dist/24/back";

import persik from "../img/persik.png";
import iconEdit from "../img/iconEdit.png";

import "./Persik.css";
import TodoList from "./components/TodoList";
import ModalAddTodo from "./components/Modal/ModalAddTodo";
import ModalDeleteNotes from "./components/Modal/ModalDeleteNotes";
import EditNotesTitle from "./components/EditNotesTitle";
import ReactDOM from "react-dom";

const osName = platform();

function Persik({ go, data, setNotes, setActivePanel }) {
  window.addEventListener("backbutton", function () {
    setActivePanel("home");
  });

  window.addEventListener("keydown", function (event) {
    if (event.key === "Escape") {
      setActivePanel("home");
    }
  });

  const notesId = data.notesId;
  let notes = "";
  if (notesId === "1") {
    notes = data.notes1;
  } else if (notesId === "2") {
    notes = data.notes2;
  } else if (notesId === "3") {
    notes = data.notes3;
  } else {
    notes = data.notes4;
  }
  let i = 0;
  const [todos, setTodos] = useState(
    notes
      .split("₪")
      .slice(1)
      .map((title) => {
        i++;
        return {
          title,
          id: i + Date.now() + title,
          completed: false,
        };
      })
  );

  function toggleTodo(id) {
    setTodos(
      todos.map((todo) => {
        if (todo.id === id) {
          todo.completed = !todo.completed;
        }
        return todo;
      })
    );
  }

  data.notesTitle = notes.split("₪")[0];
  function removeTodo(id) {
    setTodos(todos.filter((todo) => todo.id !== id));
    let title = "";
    todos.map((todo) => {
      if (todo.id === id) {
        title = todo.title;
      }
      return true;
    });
    let result = data.notesTitle;
    notes
      .split("₪")
      .slice(1)
      .map((item) => {
        if (item !== title) {
          result += "₪" + item;
        }
        return true;
      });
    if (notesId === "1") {
      data.notes1 = result;
    } else if (notesId === "2") {
      data.notes2 = result;
    } else if (notesId === "3") {
      data.notes3 = result;
    } else {
      data.notes4 = result;
    }
    setNotes(
      data.docId,
      data.userId,
      data.notes1,
      data.notes2,
      data.notes3,
      data.notes4
    );
  }

  const [editNotesTitleShowing, setEditNotesTitleShowing] = useState(false);
  function toggleEditNotesTitle() {
    setEditNotesTitleShowing(!editNotesTitleShowing);
  }

  const styles = {
    notask: {
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },
    li: {
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      padding: ".5rem 1rem",
      marginBottom: ".7rem",
    },
  };

  return (
    <Panel id="persik">
      <PanelHeader
        left={
          <PanelHeaderButton onClick={go} data-to="home">
            {osName === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
          </PanelHeaderButton>
        }
      >
        {notes.split("₪")[0]}
      </PanelHeader>
      {/* TODO */}
      <Div>
        <li style={styles.li}>
          <ModalDeleteNotes setActivePanel={setActivePanel} />
          <img
            className="btn"
            src={iconEdit}
            onClick={toggleEditNotesTitle}
            alt="Edit Title"
          />
          <ModalAddTodo todos={todos} setTodos={setTodos} />
        </li>
        {editNotesTitleShowing && (
          <EditNotesTitle
            prevTitle={data.notesTitle}
            setEditNotesTitleShowing={setEditNotesTitleShowing}
          />
        )}
        {todos.length ? (
          <TodoList
            todos={todos}
            onToggle={toggleTodo}
            removeTodo={removeTodo}
          />
        ) : (
          <p style={styles.notask}>Тут пока ничего нет :3</p>
        )}
      </Div>
      {todos.length === 0 ? (
        <img className="Persik" src={persik} alt="Persik The Cat" />
      ) : (
        <p></p>
      )}
    </Panel>
  );
}

Persik.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
};

export default Persik;
