import React from "react";
import { SimpleCell, Title } from "@vkontakte/vkui";
import iconAdd from "../../img/iconAdd.png";

export default function AddNotesView({ onCreate }) {
  return (
    <SimpleCell
      onClick={() => onCreate()}
      expandable
      before={<img className="icon" src={iconAdd} alt="Add Note" />}
    >
      <Title weight="semibold">&nbsp;&nbsp;&nbsp;Добавить заметку</Title>
    </SimpleCell>
  );
}
