import React from "react";
import iconAdd from "../../../img/iconAdd.png";
import "./Modal.css";
import AddTodo from "../AddTodo";
import data from "../../data";
import { setNotes } from "../../../database";
import { Snackbar } from "@vkontakte/vkui";

export default class ModalAddTodo extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isActive: false,
      snackbar: null,
    };
  }

  addTodo = (title) => {
    if (data.lastTitle === title) {
      console.log("duplicate " + title);
      this.setState({ snackbar: true });
    } else {
      data.lastTitle = title;
      var notesId = data.notesId;
      if (notesId === "1") {
        data.notes1 += "₪" + title;
      } else if (notesId === "2") {
        data.notes2 += "₪" + title;
      } else if (notesId === "3") {
        data.notes3 += "₪" + title;
      } else {
        data.notes4 += "₪" + title;
      }
      setNotes(
        data.docId,
        data.userId,
        data.notes1,
        data.notes2,
        data.notes3,
        data.notes4
      );
      this.setState({ isActive: false });
      this.props.setTodos(
        this.props.todos.concat({
          title,
          id: Date.now() + title,
          completed: false,
        })
      );
    }
  };

  render() {
    return (
      <React.Fragment>
        <img
          className="btn"
          src={iconAdd}
          onClick={() => this.setState({ isActive: true })}
          alt="Add Todo"
        />

        {this.state.isActive && (
          <div className="modal">
            <div
              className="modal-field"
              onClick={() => this.setState({ isActive: false })}
            />
            <div className="modal-body">
              <AddTodo onCreate={this.addTodo} />
            </div>
          </div>
        )}

        {this.state.snackbar && (
          <Snackbar
            layout="vertical"
            onClose={() => this.setState({ snackbar: false })}
          >
            Дублирование запрещено :(
          </Snackbar>
        )}
      </React.Fragment>
    );
  }
}
