import firebase from "firebase";
import data from "./panels/data";

const app = firebase.initializeApp({
  apiKey: "AIzaSyDiGB3YzRvrL0VQ3g2IXbMPWc7xCfn9ITQ",
  authDomain: "vk-app-notes.firebaseapp.com",
  databaseURL: "https://vk-app-notes.firebaseio.com",
  projectId: "vk-app-notes",
  storageBucket: "vk-app-notes.appspot.com",
  messagingSenderId: "440873597069",
  appId: "1:440873597069:web:971c8ee7d93805e2484ac4",
});

const db = app.firestore();
const collection = db.collection("notes");

let docsCount = 0;
function waitForDocScanning() {
  if (docsScanned >= docsCount) {
    if (!data.docId) {
      addNotes(data.userId, "", "", "", "");
    }
  } else {
    console.log("Scanned: " + docsScanned + "/" + docsCount);
    setTimeout(() => {
      waitForDocScanning();
    }, 100);
  }
}

let docsScanned = 0;
function findDocumentById(id) {
  collection.onSnapshot((querySnapshot) => {
    docsCount = querySnapshot.size;
    querySnapshot.forEach((doc) => {
      docsScanned++;
      if (doc.data().id === id) {
        data.docId = doc.id;
      }
    });
    waitForDocScanning();
  });
}

function addNotes(id, notes1, notes2, notes3, notes4) {
  collection.add({
    id,
    notes1,
    notes2,
    notes3,
    notes4,
  });
}

function setNotes(docId, id, notes1, notes2, notes3, notes4) {
  collection.doc(docId).set({
    id,
    notes1,
    notes2,
    notes3,
    notes4,
  });
}

export { db, setNotes, findDocumentById };
