import React from "react";
import iconDelete from "../../../img/iconDelete.png";
import "./Modal.css";
import data from "../../data";
import { setNotes } from "../../../database";
import { Button, Title } from "@vkontakte/vkui";

export default class ModalDeleteNotes extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isActive: false,
    };
  }

  deleteNotes = () => {
    var notesId = data.notesId;
    if (notesId === "1") {
      data.notes1 = "";
    } else if (notesId === "2") {
      data.notes2 = "";
    } else if (notesId === "3") {
      data.notes3 = "";
    } else {
      data.notes4 = "";
    }
    setNotes(
      data.docId,
      data.userId,
      data.notes1,
      data.notes2,
      data.notes3,
      data.notes4
    );
    this.props.setActivePanel("home");
  };

  render() {
    return (
      <React.Fragment>
        <img
          className="btn"
          src={iconDelete}
          onClick={() => this.setState({ isActive: true })}
          alt="Delete Notes"
        />

        {this.state.isActive && (
          <div className="modal">
            <div
              className="modal-field"
              onClick={() => this.setState({ isActive: false })}
            />
            <div className="modal-body">
              <div style={{ textAlign: "center" }}>
                <Title weight="semibold">Удалить заметку?</Title>
                <span className="deleteBtns">
                  <Button
                    mode="tertiary"
                    size="xl"
                    onClick={() => this.deleteNotes()}
                  >
                    Да
                  </Button>
                  <Button
                    style={{ marginLeft: 0 }}
                    mode="tertiary"
                    size="xl"
                    onClick={() => this.setState({ isActive: false })}
                  >
                    Нет
                  </Button>
                </span>
              </div>
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }
}
